.clearfix {
  overflow: hidden;
  clear: both;
}

.feedback-btn {
  padding: 12px 23px;
  border-radius: 10px;
  background-color: #e2261d;
  border: 0;
  text-shadow: 1px 1px 3px rgba(29, 29, 29, 0.7);
  color: #ffffff;
  font-family: $latoBlack;
  font-size: 12px;
  font-weight: 400;
  text-transform: uppercase;
  transition: 200ms ease;
  &:hover {
    transition: 200ms ease;
    background: darken(#e2261d, 10%);
  }
}

.mgt-25 {
  margin-top: 25px;
}
.mgb-25{
  margin-bottom: 25px;
}
.text-left{
  text-align: left;
}