.clients {
  float: left;
  width: 100%;
  position: relative;
  &__title {
    font-family: $latoBlack;
    font-size: 18px;
    font-weight: 900;
    text-transform: uppercase;
    margin-top: 40px;
    margin-bottom: 10px;
    padding: 0 80px;
    width: 100%;
  }
  &__desk {
    color: #1b1b1b;
    font-size: 14px;
    line-height: 21px;
    padding: 0 80px;
    width: 100%;
  }
  &__wrap {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    padding: 0 80px;
    margin: 50px 0;
  }
  &__item {
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 15px 30px 15px;
  }
  &__slider {
    display: flex;
    position: relative;
    margin-bottom: 126px;
  }
  &__slider-container {
    width: 460px;
    margin-right: 15px;
    position: relative;
    z-index: 1;
    margin-left: 30px;
    box-sizing: border-box;
    &:after{
      content: '';
      position: absolute;
      left: -30px;
      top: -45px;
      z-index: -1;
      width: 390px;
      height: 293px;
      background-color: #e74038;
    }
  }
  &__slider-nav {
    width: calc(100% - 475px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__warning {
    position: absolute;
    left: 50%;
    bottom: 30px;
    transform: translateX(-50%);
    width:  490px;
    color: #000000;
    font-size: 14px;
    line-height: 17px;
    span {
      color: #e74038;
    }
  }
}